/**
 * 
 * @param value - 
 * @param domain -  [, ]
 * @param range -  [, ]
 * @returns 
 */
export const linear = (value, domain, range) => {
    const [d0, d1] = domain;
    const [r0, r1] = range;
    if (d1 === d0)
        return r0;
    const ratio = (value - d0) / (d1 - d0);
    return r0 + ratio * (r1 - r0);
};
/**
 * 
 * @param value - 
 * @param domain -  [, ]
 * @param range -  [, ]
 * @returns 
 */
export const log = (value, domain, range) => {
    const [d0, d1] = domain;
    const [r0, r1] = range;
    const ratio = Math.log(value - d0 + 1) / Math.log(d1 - d0 + 1);
    return r0 + ratio * (r1 - r0);
};
/**
 * 
 * @param value - 
 * @param domain -  [, ]
 * @param range -  [, ]
 * @param exponent - 
 * @returns 
 */
export const pow = (value, domain, range, exponent = 2) => {
    const [d0, d1] = domain;
    const [r0, r1] = range;
    const ratio = Math.pow((value - d0) / (d1 - d0), exponent);
    return r0 + ratio * (r1 - r0);
};
/**
 * 
 * @param value - 
 * @param domain -  [, ]
 * @param range -  [, ]
 * @returns 
 */
export const sqrt = (value, domain, range) => {
    const [d0, d1] = domain;
    const [r0, r1] = range;
    const ratio = Math.sqrt((value - d0) / (d1 - d0));
    return r0 + ratio * (r1 - r0);
};
