/**
 * <zh/> 
 *
 * <en/> Create a plugin container
 * @param type - <zh/>  | <en/> plugin type
 * @param cover - <zh/>  | <en/> Whether the container covers the entire canvas
 * @param style - <zh/>  | <en/> Additional style
 * @returns <zh/>  | <en/> plugin container
 */
export function createPluginContainer(type: string, cover = true, style?: Partial<CSSStyleDeclaration>): HTMLElement {
  const container = document.createElement('div');

  container.setAttribute('class', `g6-${type}`);

  Object.assign(container.style, {
    position: 'absolute',
    display: 'block',
  });

  if (cover) {
    Object.assign(container.style, {
      position: 'unset',
      gridArea: '1 / 1 / 2 / 2',
      inset: '0px',
      height: '100%',
      width: '100%',
      overflow: 'hidden',
      pointerEvents: 'none',
    });
  }

  if (style) Object.assign(container.style, style);

  return container;
}

/**
 * <zh/>  DOM 
 *
 * <en/> Create a DOM element, if exists, remove it and create a new one.
 * @param id - <zh/> id | <en/> id
 * @param tag - <zh/>  | <en/> tag
 * @param style - <zh/>  | <en/> style
 * @param innerHTML - <zh/>  | <en/> innerHTML
 * @param container - <zh/>  | <en/> container
 * @returns <zh/>  DOM  | <en/> created DOM element
 */
export function insertDOM(
  id: string,
  tag = 'div',
  style: Partial<CSSStyleDeclaration> = {},
  innerHTML = '',
  container: HTMLElement = document.body,
) {
  const dom = document.getElementById(id);
  if (dom) dom.remove();

  const div = document.createElement(tag);
  div.innerHTML = innerHTML;
  div.id = id;

  Object.assign(div.style, style);

  container.appendChild(div);

  return div;
}
